import React from 'react';
import Sketch from 'react-p5';

let order=4;
let N=Math.pow(2,order);
let total=N*N;
let path=[];
let counter=1;
let increment = 0.2;
let widtt=500;
for(let i=0;i<total;i++)
{
	path.push([0,0]);
}
function hilbert(i)
{
	const a=[[0,0],[0,1],[1,1],[1,0]];
	
	let index=i&3;
	const k= a[index];
    for (let j=1;j<order;j++)
    {
        i=i>>2;
        let len=Math.pow(2,j);
        index=i&3
        if(index==0){
            let temp=k[0]
            k[0]=k[1]
            k[1]=temp
        }
        else if(index==1){
            k[1]=k[1]+len
        }
        else if (index==2) {
            k[1]=k[1]+len;
            k[0]=k[0]+len;
        } 
        else if(index==3){
            let temp=len-1-k[0]
            k[0]=len-1-k[1]
            k[1]=temp
            k[0]=k[0]+len;
            
        }
    }

return k;

}


const setup=(p5,parent)=>{
    p5.createCanvas(7*(p5.windowWidth)/8,7*(p5.windowWidth)/8).parent(parent);
    widtt=p5.width;
    for(let i=0;i<total;i++){
		path[i]=hilbert(i);
		let len=p5.width/N
		path[i][0]=path[i][0]*len+(len/2);
		path[i][1]=path[i][1]*len+(len/2);
		
	}


}
const draw=p5=> 
{
    
    p5.background(0);
	p5.stroke(255);
	p5.noFill()
	// p5.beginShape();
	
	for(let i=1;i<counter;i++){
		 p5.stroke(255,(170+i)%255,(100+i)%255)
		
	p5.line(path[i][0],path[i][1],path[i-1][0],path[i-1][1])
	}
	// p5.endShape();
	counter+=increment;
	if (counter>=path.length){
		counter=path.length;
		increment=0;
	}



}
const changeorder=(e)=>{

    order=e.target.value;
 
    counter=0
    N=Math.pow(2,order);
 total=N*N;
 path=[];
 for(let i=0;i<total;i++)
    {
		path[i]=hilbert(i);
		let len=widtt/N
		path[i][0]=path[i][0]*len+(len/2);
        path[i][1]=path[i][1]*len+(len/2);
    }
    if (order<=4){
        increment=0.2;
    }
    else if (order <=6 ){
        increment=4;
    }
    else if(order <8) {
        increment=60;
    }
    else if(order ==8){
        increment=400
    }
    
    }


export default function Hilbert() 
{
    return (
        <div>
             <center><h2>HILBERT's space filling curve</h2></center><br />
            <label>change order value:</label>
             <input id="typeinp" type="range" min="3" max="8" defaultValue="4" onChange={changeorder} step="1"/>
             
            <Sketch setup={setup} draw={draw} />
            
        </div>
    );
}
