import React ,{useState} from 'react';
import Sketch from 'react-p5';

let n=6,d=61;

const changen=(e)=>{

    n=e.target.value;
    console.log(n,d);
    


}
const changed=(e)=>{

    d=e.target.value;
    console.log(n,d);
    


}

const setup=(p5,parent)=>{
p5.createCanvas(p5.displayWidth,p5.displayHeight/2).parent(parent);
p5.angleMode(p5.DEGREES);




}
const draw=p5=>{
p5.background(0);
p5.stroke(255,150,150);
p5.translate(p5.width/2,p5.height/2)
p5.noFill()
p5.beginShape()
for(let i=0;i<361;i++){
    let k=i*d;
    let r=150*p5.sin(n*k);
    let x=r*p5.cos(k);
    let y=r*p5.sin(k);
    p5.vertex(x,y);


}


p5.endShape()



let x=5;


}




export default function Maurerrose() {
  
    return (
        <div><center><h2>MAURER ROSE</h2></center><br />
            <label>change N value:</label>
             <input id="typeinp" type="range" min="2" max="7" defaultValue="6" onChange={changen} step="1"/>
             <label>change D value:</label>
             <input id="typeinp" type="range" min="26" max="71" defaultValue="61" onChange={changed} step="5"/>
  
            
            
            <Sketch setup={setup} draw={draw} />
        </div>
    )
}
